/**
 * LOCALES CONTEXT.
 * 
 * Provide multi languages support.
 */

 /**
 * Dependencies.
 */
import * as React from 'react'

/**
 * Load the locales files.
 */
const config = require('../../../gatsby-config')
const { locales } = config.siteMetadata
let i18n = []
locales.langs.forEach( (lng) => {
  i18n = [
    ...i18n,
    {
      langKey: lng,
      locales: require(`../../.config/locales-${lng}.js`)
    }
  ]
  //const i18nFile = require(`./src/.config/locales-${lng}.js`)
  //i18n404[lng] = { PageNotFoundTitle: i18nFile.PageNotFoundTitle, PageNotFoundDescription: i18nFile.PageNotFoundDescription }
})

/**
 * Locales Context object.
 */
export const LocalesContext = React.createContext()

/**
 * React Function Component (JSX).
 * 
 * @param children      Child React Components.
 */
const LocalesContextProvider = ({ pageContext, children }) => {
  
  let params = pageContext
  if( typeof pageContext.langKey === 'undefined' ) {
    params = children?.props?.data?.mdx?.fields
    if( typeof params === 'undefined' ) {
      params = {langKey: 'en'}
    }
  }

  return (
    <LocalesContext.Provider value={{...params, i18n}}>{children}</LocalesContext.Provider>
  )

}

export default LocalesContextProvider