/**
 * GLOBAL CONTEXT PROVIDER.
 *
 * Used to keep a persistent state between page transitions and refresh for 
 * the current user authentication and preferences.
 * 
 */

/**
 * Dependencies.
 */
import React, { useState } from "react"
import { ls, isBrowser } from "../services/auth"
import config from "../../gatsby-config"
const { authentication } = config.siteMetadata

/**
 * Context State and Dispatch.
 */
export const StateContext = React.createContext()
export const DispatchContext = React.createContext()
StateContext.displayName = "Context (State)"
DispatchContext.displayName = "Context (Dispatch)"

/**
 * Context State data.
 */
const initialState = {
  theme: "light",
  authenticated: false,
  user: {},
}

/**
 * Reducer.
 *
 * @param {*} state
 * @param {*} action
 */
function reducer(state, action) {
  switch (action.type) {
    case "SET_THEME": {
      const theme = state.theme === "light" ? "dark" : "light"
      if( isBrowser() ) window.localStorage.setItem('theme', theme)
      return {
        ...state,
        theme: theme,
      }
    }
    case "SET_AUTH": {
      return {
        ...state,
        authenticated: action.authenticated,
        user: action.user,
      }
    }
    default:
      throw new Error("Bad Action Type")
  }
}

/**
 * Color mode.
 */
function getInitialColorMode() {
  const persistedColorPreference = isBrowser() ? window.localStorage.getItem('theme') : null
  const hasPersistedPreference = typeof persistedColorPreference === 'string'
  // If the user has explicitly chosen light or dark,
  // let's use it. Otherwise, this value will be null.
  if (hasPersistedPreference) {
    return persistedColorPreference
  }
  // If they haven't been explicit, let's check the media
  // query
  if( isBrowser() ) {
    const mql = window.matchMedia('(prefers-color-scheme: dark)')
    const hasMediaQueryPreference = typeof mql.matches === 'boolean'
    if (hasMediaQueryPreference) {
      const theme = mql.matches ? 'dark' : 'light'
      //window.localStorage.setItem('theme', theme)
      return theme
    }
  }
  // If they are using a browser/OS that doesn't support
  // color themes, let's default to 'light'.
  return 'light'
}

/**
 * React Function Component (JSX).
 *
 * @param children      Child React Components.
 */
const GlobalContextProvider = ({ children }) => {

  // Secure
  const [state, dispatch] = React.useReducer(
    reducer,
    ls ? ls.get("session") || initialState : initialState
  )
  //const [state, dispatch] = React.useReducer(reducer, JSON.parse(localStorage.getItem('session')) || initialState)

  const [colorMode, setColorMode] = React.useReducer(
    reducer,
    { theme: getInitialColorMode() }
  )

  return (
    <StateContext.Provider value={state, colorMode}>
      <DispatchContext.Provider value={dispatch, setColorMode}>
        {children}
      </DispatchContext.Provider>
    </StateContext.Provider>
  )

}

export default GlobalContextProvider
