import React from "react"
import PropTypes from "prop-types"
import imgSiteLogo from "../../static/img/guy-dumais-icon.svg"
//import imgSiteLogo from "../images/guy-dumais-logo.svg"
//import imgSiteLogo from "../../static/img/guy-dumais-logo.svg"
//import { isHome } from '../services/utils'

/*import { useStaticQuery, graphql } from "gatsby"*/
/*import Img from "gatsby-image"*/

/*
 * This component is built using `gatsby-image` to automatically serve optimized
 * images with lazy loading and reduced file sizes. The image is loaded using a
 * `useStaticQuery`, which allows us to load the image from directly within this
 * component, rather than having to pass the image data down from pages.
 *
 * For more information, see the docs:
 * - `gatsby-image`: https://gatsby.dev/gatsby-image
 * - `useStaticQuery`: https://www.gatsbyjs.org/docs/use-static-query/
 */

const SiteLogo = ({ src, alt }) => {
  return <img src={imgSiteLogo} alt={alt} width="52" height="52" />
  /*return <img src={imgSiteLogo} alt={alt} width="240.388" height="52" />*/
  /*return <img src={ !isHome() && context.theme === 'light' ? imgSiteLogo : imgSiteLogoDark} alt={alt} width="248.656" height="48" />*/
}

SiteLogo.propTypes = {
  src: PropTypes.string,
  alt: PropTypes.string,
}

SiteLogo.defaultProps = {
  src: ``,
  alt: ``,
}

export default SiteLogo
