/**
 * AUTH.
 * 
 * oAuth 2.0 module for handling user's authentication.
 */
import SecureLS from 'secure-ls'
import { StateContext } from '../.gatsby/GlobalContextProvider'
import config from '../../gatsby-config'
const { authentication } = config.siteMetadata

/**
 * IS BROWSER.
 * 
 * Gatsby specific: Used to skip server-side rendering (gatsby-ssr.js).
 */
export const isBrowser = () => typeof window !== "undefined"

/**
 * ENCRYPTED LOCAL STORAGE CLIENT.
 */
export const ls = isBrowser() ? new SecureLS({encodingType: 'aes'}) : null

/**
 * IS AUTHENTICATED.
 */
export const isAuthenticated = () => {

  // SSR bypass
  if (!isBrowser()) return

  // Return the current authenticated state from global context.
  if (typeof StateContext._currentValue !== 'undefined' && typeof StateContext._currentValue.authenticated !== 'undefined') {
    //console.log('isAuthenticated > Context authenticated: %s', StateContext._currentValue.authenticated)
    return StateContext._currentValue.authenticated
  }

  // Return the current authenticated state from localStorage.
  const user = getUser()
  if (user && Object.keys(user).length > 0) {
    //console.log('isAuthenticated > getUser: %s', user)
    return !!user.id
  }

  // Not authenticated.
  //console.log('isAuthenticated > Not authenticated')
  return false

}

/**
 * CHECK USER.
 */
export const checkUser = (contextAuthenticated, cb) => {

  // SSR bypass
  if (!isBrowser()) return

  if (contextAuthenticated) return cb(getUser()) 
  //if (isAuthenticated()) return cb(getUser())

  fetch(authentication.api + '/', {
    method: 'GET',
    mode: 'cors',
    credentials: 'include',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      'Access-Control-Allow-Credentials': true
    }
  })
    .then(response => {
      if (response.status === 200) return response.json()
      //throw new Error("Authentication failed!")
    })
    .then(responseJson => {
      //setUser(responseJson.user)
      return cb(responseJson.user)
    })
    .catch(error => {
      //console.error(error)
      return cb(false)
    })

}

/**
 * GET USER
 */
export const getUser = () => {

  // SSR bypass
  if (!isBrowser()) return

  // Return session data from encrypted local storage.
  return ls.get('session')
    ? ls.get('session').user
    : {}

  // Return session data from local storage.
  /*
  return localStorage.getItem('session')
    ? JSON.parse(localStorage.getItem('session')).user
    : {}
  */

}

/**
 * HANDLE LOGIN.
 */
export const handleLogin = (event) => {

  // SSR bypass
  if (!isBrowser) return

  if (event.target.id === 'google') {
    window.open(authentication.api + `/login/${event.target.id}`, "_self")
  }

}

/**
 * LOGOUT.
 */
export const logout = () => {

  // SSR bypass
  if (!isBrowser) return

  ls.removeAll()
  //localStorage.removeItem('session')
  window.open(authentication.api + '/logout', '_self')

}