// Global stylesheet
import './src/styles/global.css'

// wrapRootElement Api
export { default as wrapRootElement } from './src/.gatsby/wrapRootElement'

// wrapPageElement Api
export { default as wrapPageElement } from './src/.gatsby/wrapPageElement'

// PWA Update
export const onServiceWorkerUpdateReady = () => {
  const answer = window.confirm(
    `This application has been updated. ` +
      `Reload to display the latest version?`
  )

  if (answer === true) {
    window.location.reload()
  }
}

