/**
 * CART BUTTON.
 */

/**
 * Dependencies.
 */
import React, {useContext} from 'react'
//import useSession from '../account/useSession'
//import { logout } from '../../services/auth'
import { navigate } from 'gatsby'
import { LocalesContext } from '../locales/LocalesContextProvider'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShoppingCart } from '@fortawesome/free-solid-svg-icons'

/**
 * Load site config.
 */
import config from '../../../gatsby-config'
const { locales } = config.siteMetadata

/**
 * FA ICON COMPONENTS.
 */
const FaShoppingCartIcon = () => <FontAwesomeIcon icon={faShoppingCart} />

/**
 * Button function component.
 */
export default () => {

  //const session = useSession()
  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const { logout: logoutTxt, signin: signinTxt } = i18nData.locales
  const loginLink = langKey === locales.defaultLangKey ? '/account/login' : `/${langKey}/account/login`

  return (
    <>
      <button id="shopping-cart" aria-label="Shopping Cart">
        <FaShoppingCartIcon key="cartIcon" />
      </button>
    </>
  )

}
