/**
 * AUTHENTICATE BUTTON.
 */

/**
 * Dependencies.
 */
import React, {useContext} from 'react'
import useSession from '../account/useSession'
import { logout } from '../../services/auth'
import { navigate } from 'gatsby'
import { LocalesContext } from '../locales/LocalesContextProvider'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUser } from '@fortawesome/free-solid-svg-icons'

/**
 * Load site config.
 */
import config from '../../../gatsby-config'
const { locales } = config.siteMetadata

/**
 * FA ICON COMPONENTS.
 */
const FaUserIcon = () => <FontAwesomeIcon icon={faUser} />

/**
 * Button function component.
 */
const AuthenticateButton = () => {

  const session = useSession()
  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const { logout: logoutTxt, signin: signinTxt } = i18nData.locales
  const loginLink = langKey === locales.defaultLangKey ? '/account/login' : `/${langKey}/account/login`

  return (
    <>
      <button
        id="authentication"
        aria-label={session.authenticated ? logoutTxt : signinTxt}
        onClick={event => {
          event.preventDefault()
          session.authenticated ? logout() : navigate(loginLink)
        }}
      >
        <FaUserIcon key="userIcon" />
        {/*session.authenticated ? 'Déconnecter' : 'Connecter' : session.authenticated ? 'Logout' : 'Sign in'*/}
        {/* session.authenticated ? logoutTxt : [<FaUserIcon key="userIcon" />,signinTxt] */}
        
        {/* session.authenticated ? logoutTxt : <FaUserIcon key="userIcon" /> */}
      </button>
    </>
  )

}

export default AuthenticateButton