/**
 * RESPONSIVE PLAYER.
 */

/**
 * Dependencies.
 */
import React from 'react'
import ReactPlayer from 'react-player/youtube'

/**
 * Class Component.
 */
class ResponsivePlayer extends React.Component {

  render () {
    return (
      <div className='player-wrapper'>
        <ReactPlayer
          className='react-player'
          url={this.props.url}
          width='100%'
          height='100%'
        />
      </div>
    )
  }

}

export default ResponsivePlayer