import React, { useContext, useState } from 'react'
import { DispatchContext, StateContext } from '../../.gatsby/GlobalContextProvider'
import { LocalesContext } from '../locales/LocalesContextProvider'
import { navigate } from '@reach/router'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLanguage, faAdjust, faSignInAlt, faSignOutAlt, faUser, faCog, faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons'
import { CSSTransition } from 'react-transition-group'
//import ColorModeToggle from './ColorModeToggle'

/**
 * Load site config.
 */
//import config from '../../../gatsby-config'
//const { locales } = config.siteMetadata

/**
 * FA ICON COMPONENTS.
 */
const FaAdjustIcon = () => <FontAwesomeIcon icon={faAdjust} />
const FaLanguageIcon = () => <FontAwesomeIcon icon={faLanguage} />
const FaSigninIcon = () => <FontAwesomeIcon icon={faSignInAlt} />
const FaSignoutIcon = () => <FontAwesomeIcon icon={faSignOutAlt} />
const FaUserIcon = () => <FontAwesomeIcon icon={faUser} />
const FaCogIcon = () => <FontAwesomeIcon icon={faCog} />
const FaChevronLeftIcon = () => <FontAwesomeIcon icon={faChevronLeft} />
const FaChevronRightIcon = () => <FontAwesomeIcon icon={faChevronRight} />

/**
 * Dropdown Component (JSX).
 */
export default () => {

  // i18n.
  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const { 
    darkMode: darkModeTxt,
    lightMode: lightModeTxt,
    colorMode: colorModeTxt,
    settings: settingsTxt,
    signin: singinTxt,
    logout: logoutTxt,
    profile: profileTxt,
    language: languageTxt,
    mainMenu: mainMenuTxt
  } = i18nData.locales

  // Context.
  const dispatch = useContext(DispatchContext)
  const context = useContext(StateContext)

  // Session state variables.
  const [colorMode, setColorMode] = useState(context)

  function handleColorToggle() {
    document.documentElement.classList.toggle("colors-" + colorMode.theme)
    const theme = colorMode.theme === "light" ? "dark" : "light"
    dispatch({ type: 'SET_THEME' })
    setColorMode({...context, theme: theme })
    //if (ls) ls.set('theme', {...context, theme: colorMode })
    document.documentElement.classList.toggle("colors-" + theme)
  }
  
  const [activeMenu, setActiveMenu] = useState('main')

  const [menuHeight, setMenuHeight] = useState(null)
  const calcHeight = (el) => {
    const height = el.offsetHeight
    setMenuHeight(height)
  }

  const DropdownItem = (props) => {
    return (
      <a href="#" className="menu-item" onClick={( event ) => props?.onClickOverride ? props.onClickOverride() : props.goToMenu && setActiveMenu(props.goToMenu)}>
        <span className="icon-button">{props.leftIcon}</span>
        {props.children}
        <span className="icon-right">{props.rightIcon}</span>
      </a>
    )
  }

  return (
    <div className="dropdown" style={{ height: menuHeight }}>
        <CSSTransition
        onEnter={calcHeight}
        in={activeMenu === 'main'}
        timeout={150}
        classNames="menu-primary"
        unmountOnExit>

        <div className="menu">
          <DropdownItem
            leftIcon={<FaUserIcon />}>
            {profileTxt}
          </DropdownItem>
          <DropdownItem
            leftIcon={<FaSignoutIcon />}
          >
            {logoutTxt}
          </DropdownItem>
          <DropdownItem
            leftIcon={<FaCogIcon />}
            rightIcon={<FaChevronRightIcon />}
            goToMenu="settings">
            {settingsTxt}
          </DropdownItem>
        </div>
      </CSSTransition>

      <CSSTransition
        onEnter={calcHeight}
        in={activeMenu === 'settings'}
        timeout={150}
        classNames="menu-secondary"
        unmountOnExit>

        <div className="menu">
          <DropdownItem goToMenu="main" leftIcon={<FaChevronLeftIcon />}>
            {mainMenuTxt}
          </DropdownItem>
          <DropdownItem leftIcon={<FaAdjustIcon />} onClickOverride={handleColorToggle}>
          {colorMode.theme === "light" ? darkModeTxt : lightModeTxt}
          </DropdownItem>
          <DropdownItem goToMenu="language" leftIcon={<FaLanguageIcon />} rightIcon={<FaChevronRightIcon />}>
            {languageTxt}
          </DropdownItem>
        </div>
      </CSSTransition>

      <CSSTransition
        onEnter={calcHeight}
        in={activeMenu === 'language'}
        timeout={150}
        classNames="menu-secondary"
        unmountOnExit>

        <div className="menu">
          <DropdownItem goToMenu="settings" leftIcon={<FaChevronLeftIcon />}>
            {settingsTxt}
          </DropdownItem>
          <DropdownItem leftIcon={<FaLanguageIcon />} onClickOverride={() => navigate('/')}>
            English
          </DropdownItem>
          <DropdownItem leftIcon={<FaLanguageIcon />} onClickOverride={() => navigate('/fr/')}>
            Français
          </DropdownItem>
        </div>
      </CSSTransition>

    </div>
  )

}