/**
 * GOOGLE TRENDS WIDGET
 * 
 * 
 */
import React from 'react'
import ReactDOM from 'react-dom'
import { PropTypes } from 'prop-types'

export default class WidgetGoogleTrends extends React.Component {

  static propTypes = {
    keyword: PropTypes.string.isRequired,
    lang: PropTypes.string,
    geo: PropTypes.string,
    type: PropTypes.string,
    url: PropTypes.string,
    time: PropTypes.string
  }
  
  static defaultProps = {
    keyword: '',
    lang: 'en',
    geo: '',
    type: 'TIMESERIES',
    url: 'https://ssl.gstatic.com/trends_nrtr/2213_RC01/embed_loader.js',
    time: 'today 12-m'
  }


  componentDidMount() {
    const { keyword, lang, type, url, geo, time } = this.props

    this.clear()
    
    let instance = this
    let script = document.createElement('script')
    script.src = url
    script.async = true

    ReactDOM.findDOMNode(instance.refs.charts).appendChild(script)

    script.onload = function() {
      window.trends.embed.renderExploreWidgetTo(
        ReactDOM.findDOMNode(instance.refs.charts),
        type,
        {
          comparisonItem: [
            { keyword: keyword, geo: geo, time: time }
          ],
          category: 0,
          property: ''
        },
        {
          exploreQuery: `q=${keyword}&${time}`,
          guestPath: 'https://trends.google.com:443/trends/embed/',
          locale: `${lang}`
        }
      )
    }
  }

  render() {
    return (
      <div ref="charts" />
    )
  }

  clear() {
    var iframes = document.querySelectorAll('iframe')
    for (var i = 0; i < iframes.length; i++) {
      iframes[i].parentNode.removeChild(iframes[i])
    }
  }

}
