/**
 * LAYOUT
 * 
 * Main layout for all posts and pages of the site.
 * 
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

/**
 * Dependencies.
 */
import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { useStaticQuery, graphql } from 'gatsby'
import SEO from './seo'
//import {Breadcrumb} from 'gatsby-plugin-breadcrumb'
//import { useBreadcrumb } from 'gatsby-plugin-breadcrumb'
//import Breadcrumb from './Breadcrumb2'
import { LocalesContext } from '../components/locales/LocalesContextProvider'
//import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
//import { faUser, faCalendarAlt } from '@fortawesome/free-solid-svg-icons'
//import styled from 'styled-components'
import Header from './site-header'
import Footer from './site-footer'

/**
 * FA ICON COMPONENTS.
 */
//const FaUserIcon = () => <FontAwesomeIcon icon={faUser} />
//const FaDateIcon = () => <FontAwesomeIcon icon={faCalendarAlt} />

/**
 * Dynamic Styles.
 */
/*
const BgCover = styled.div`
  background-image: ${props => `url(${props.bgImage})`};
  background-repeat: no-repeat;
  background-position: right;

  @media (max-width: 768px) {
    display: flex;
  }
`
*/

/**
 * Layout React component (JSX).
 */
const Layout = ({ pageMeta, children, location, pageContext }) => {

  //console.log('pageMeta: ', pageMeta)
  //console.log('pageContext: ', pageContext)

  // Get the locales context
  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const { author } = i18nData?.locales
  const mdx = pageMeta?.data?.mdx || pageMeta?.pageContext
  //console.log('mdx: ', mdx)
  /*
  const { crumbs } = useBreadcrumb({
    location,
    crumbLabel: pageMeta.title,
    crumbSeparator: ' / ',
  })
  */

  /**
   * Define the id attribute of the html tag.
   */
  let pageName = 'home'
  let pageUrl = location.pathname
  const slug = pageContext.slug
  if (slug === '/404/') {
    pageName = 'PageNotFound'
  } else {
    const reg = new RegExp(`/${langKey}`)
    pageUrl = pageUrl.replace(reg, '')
    if (pageUrl !== '/' && pageUrl !== `/${langKey}/`) {
      pageName = pageUrl.replace(/\//g, '')
    }
  }

  /**
   * Get the site meta data.
   */
  const data2 = useStaticQuery(
    graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
          description
          siteUrl
          siteLogo
        }
      }
    }
  `)

  /**
   * Deconstruct Meta used by layout.
   */
  const { title, siteLogo } = data2.site.siteMetadata
  //const json = JSON.stringify(pageMeta, null, 2)  

  /**
   * Cover.
   */
  /*
  const tags = pageMeta?.tags
  const metaDateOptions = pageMeta?.date ? { year: 'numeric', month: 'long', day: 'numeric' } : null
  const metaDate = pageMeta?.date ? pageMeta.date.toLocaleDateString(pageContext.langKey, metaDateOptions) : null
  */

  /**
   * Template
   */
  const template = mdx?.frontmatter?.template || 'page'

  /**
   * Return the layout components.
   */
  return (
    <>
      <SEO
        title={pageMeta?.title ? pageMeta.title : mdx?.frontmatter?.title || null}
        description={pageMeta?.description ? pageMeta.description : mdx?.frontmatter?.description || null}
        lang={langKey}
        category={pageMeta?.category ? pageMeta.category : mdx?.frontmatter?.category || null}
        tags={pageMeta?.tags ? pageMeta.tags : mdx?.frontmatter?.tags || null}
        pageId={pageName}
        template={template}
        author={author}
        date={mdx?.frontmatter?.date}
        image={mdx?.frontmatter?.featuredImage?.childImageSharp.fluid.src}
        languageAlternates={mdx?.frontmatter?.languageAlternates}
      />
      <Header
        siteTitle={title}
        siteLogo={siteLogo}
      />
      {/* pageName !== 'home' && pageMeta.breadcrumb && <Breadcrumb pageMeta={pageMeta} /> */}
      {/* pageName !== 'home' ? <Breadcrumb crumbs={crumbs} /> : null */}
      {/* pageName !== 'home' ? <Breadcrumb location={location} crumbLabel={pageMeta.title} crumbSeparator=" / " /> : null */}
      <main>
        {children}
      </main>
      <Footer
        siteTitle={title}
      />
    </>
  )

}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  pageMeta: PropTypes.object.isRequired,
  subLayout: PropTypes.object
  /*title: PropTypes.string*/
}

/*
Layout.props = (
  <Location>
    {locationProps => <Layout {...locationProps} {...props} />}
  </Location>
)
*/

export default Layout
