/**
 * NAVBAR.
 */

/**
* Dependencies.
*/
import React, { useState, useContext } from 'react'
import { Link as GatsbyLink } from 'gatsby'
import styled from 'styled-components'
import SiteLogo from '../site-logo'
import AuthenticateButton from './AuthenticateButton'
import ShoppingCartButton from './ShoppingCartButton'
import { LocalesContext } from '../locales/LocalesContextProvider'
import NavItem from './NavItem'
import DropDownMenu from './DropDownMenu'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes, faEllipsisH, faEllipsisV, faAngleDown, faChevronDown, faChevronUp } from '@fortawesome/free-solid-svg-icons'

/**
 * Load site config.
 */
import config from '../../../gatsby-config'
const { logo, title, locales, authentication } = config.siteMetadata

/**
 * FA ICON COMPONENTS.
 */
const FaEllipsisHIcon = () => <FontAwesomeIcon icon={faEllipsisH} />
const FaEllipsisVIcon = () => <FontAwesomeIcon icon={faEllipsisV} />
const FaTimesIcon = () => <FontAwesomeIcon icon={faTimes} />
const FaAngleDownIcon = () => <FontAwesomeIcon icon={faAngleDown} />
const FaChevronDownIcon = () => <FontAwesomeIcon icon={faChevronDown} />
const FaChevronUpIcon = () => <FontAwesomeIcon icon={faChevronUp} />

/**
 * Styled components.
 */
/*const Navigation = styled.nav``*/

/*
const Navigation = styled.nav`
  height: 10vh;
  display: flex;
  background-color: #fff;
  position: relative;
  justify-content: space-between;
  text-transform: uppercase;
  border-bottom: 2px solid #33333320;
  margin: 0 auto;
  padding: 0 5vw;
  z-index: 2;
  align-self: center;

  @media (max-width: 768px) {
    position: sticky;
    height: 8vh;
    top: 0;
    left: 0;
    right: 0;
    left: 0;
  }
`
*/

const Toggle = styled.div`
  display: none;
  margin: auto 0;
  margin-left: var(--spacing-xsmall);
  /*height: 32px;*/
  /*cursor: pointer;*/
  /*padding: 0 10vw;*/
  z-index: 8;
  @media (max-width: 768px) {
    display: block;
  }
`
const Navbox = styled.div`
  @media (max-width: 768px) {
    position: fixed;
    top: 0;
    left: 0;
    /*left: ${props => (props.open ? "-100vw" : "0")};*/
    overflow-y: scroll;
    flex-direction: column;
    justify-content: center;
    width: 100vw!important;
    display: ${props => (props.open ? "flex" : "none")}!important;
    height: ${props => (props.open ? "100vh" : "0")}!important;
    color: var(--color-text);
    background: var(--color-main-bg);
    /*transition: all 0.1s ease-in;*/
  }
`
const Hamburger = styled.div`
  background-color: var(--main-brand);
  width: 30px;
  height: 3px;
  /*transition: all .1s linear;*/
  align-self: center;
  position: relative;
  transform: ${props => (props.open ? "rotate(-45deg)" : "inherit")};
  transition: none;

  ::before,
  ::after {
    width: 30px;
    height: 3px;
    background-color: var(--main-brand);
    content: "";
    position: absolute;
    /*transition: all 0.1s linear;*/
  }

  ::before {
    transform: ${props => props.open ? "rotate(-90deg) translate(-10px, 0px)" : "rotate(0deg)"};
    top: -10px;
  }

  ::after {
    opacity: ${props => (props.open ? "0" : "1")};
    transform: ${props => (props.open ? "rotate(90deg) " : "rotate(0deg)")};
    top: 10px;
  }
`

/**
 * React component (JSX).
 */
const Navbar = () => {

  // Load the localized strings
  const { langKey, i18n } = useContext(LocalesContext)
  const homeLink = langKey === locales.defaultLangKey ? `/` : `/${langKey}/`
  const i18nData = i18n.find(el => el.langKey === langKey)
  const { main } = i18nData.locales.menus

  // Mobile Navbar State
  const [navbarOpen, setNavbarOpen] = useState(false)

  /**
   * Return the navbar component.
   */
  return (
    <nav id="main-menu" role="navigation">
      <GatsbyLink id="logo" to={homeLink}>
        <SiteLogo src={logo} alt={title} />
      </GatsbyLink>
      <Navbox id="navbox" {...navbarOpen ? { open: true } : { open: false }}>
        {/*<MainMenu open={navbarOpen} />*/}
        {
        Object.keys(main).map((key) =>
        <GatsbyLink key={key} to={main[key]} onClick={() => setNavbarOpen(!navbarOpen)}>{key}</GatsbyLink>
        )
        }
      </Navbox>
      <ShoppingCartButton />
      {authentication.enable ? <AuthenticateButton /> : null}
      <NavItem id="settings-toggle" iconClosed={<FaEllipsisHIcon/>} iconOpened={<FaTimesIcon/>}>
          <DropDownMenu></DropDownMenu>
      </NavItem>
      <Toggle id="hamburger-toggle" navbarOpen={navbarOpen} onClick={() => setNavbarOpen(!navbarOpen)}>
        <Hamburger id="hamburger-icon" {...navbarOpen ? { open: true } : { open: false }} />
      </Toggle>
    </nav>
  )

}

export default Navbar