/**
 * SITE FOOTER
 * 
 * Main footer for all pages of the site.
 */

/**
 * Dependencies.
 */
import React, { useContext } from 'react'
import { Link as GatsbyLink } from 'gatsby'
import PropTypes from 'prop-types'
import { LocalesContext } from './locales/LocalesContextProvider'
import imgSiteLogo from "../../static/img/guy-dumais-logo-white.svg"

/**
 * Load site config.
 */
import config from '../../gatsby-config'
const { title } = config.siteMetadata

/**
 * React component (JSX).
 */
const Footer = ({ siteTitle }) => {

  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const { footer } = i18nData.locales.menus

  return (
  <>
  <footer id="site-footer">
    <div className="container">
      <div id="brand">
        <img src={imgSiteLogo} alt={title} width="240" />
        <p id="copyright">{`© ${new Date().getFullYear()}, ${siteTitle}.`}</p>
      </div>
      <p className="menu">
      {
      Object.keys(footer).map((key) =>
      <GatsbyLink key={key} to={footer[key]}>{key}</GatsbyLink>
      )
      }
      </p>
    </div>
  </footer>
  </>
  )

}

Footer.propTypes = {
  siteTitle: PropTypes.string,
}

Footer.defaultProps = {
  siteTitle: ``,
}

export default Footer
