/**
 * USE SESSION.
 * 
 * User session state management.
 */

/**
 * Load dependencies.
 */
import { useState, useEffect, useContext } from 'react'
import { ls, checkUser } from '../../services/auth'
import { DispatchContext, StateContext } from '../../.gatsby/GlobalContextProvider'

/**
 * Function component.
 */
export default function useSession() {

    // Context.
    const dispatch = useContext(DispatchContext)
    const context = useContext(StateContext)

    // Session state variables.
    // const [session, setSession] = useState( () => JSON.parse(localStorage.getItem('session')) || { ...context, authenticated: false, user: {} } )
    // const [session, setSession] = useState( () => JSON.parse(localStorage.getItem('session')) || context )
    // const [session, setSession] = useState( () => ls.get('session') || context )
    const [session, setSession] = useState(context)

    useEffect(() => {
        function handleStatusChange(data) {
            if (data) {
                dispatch({ type: 'SET_AUTH', authenticated: true, user: data })
                setSession({...context, authenticated: true, user: data })
                if (ls) ls.set('session', {...context, authenticated: true, user: data })
                //localStorage.setItem('session', JSON.stringify({...context, authenticated: true, user: data }))
            } else {
                //dispatch({ type: "SET_AUTH", authenticated: false, user: {} })
                //setSession(context)
                if (ls) ls.remove('session')
                //localStorage.removeItem('session')
            }
            //setSession(context)
        }
        if (!context.authenticated) checkUser(context.authenticated, handleStatusChange)   // TO DO: Time out? Expiration?
        /* Clean up
        return () => {
        ChatAPI.unsubscribeFromFriendStatus(friendID, handleStatusChange)
        }
        */
    }, [context.authenticated])
    //})

    return session

}