import React, { useState, useEffect } from 'react'
import styled from 'styled-components'

/**
 * Styled components.
 */
const NavItemWrap = styled.span`
  width: var(--button-size);
  height: var(--button-size);
  max-width: var(--button-size);
  max-height: var(--button-size);
  /*display: block;*/
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: var(--spacing-xxsmall);
  z-index: 8;
`

const IconButton = styled.button`
  border-radius: 50%;
  height: var(--button-size);
  width: var(--button-size);
  max-width: var(--button-size);
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: center;
  /*background: ${props => (props.opened ? "var(--main-brand)" : "var(--color-main-bg-alt)")}!important;*/
  /*color: ${props => (props.opened ? "var(--light-shades)" : "var(--color-text)")}!important;*/
  background: ${props => (props.opened ? "var(--main-brand)" : "transparent")}!important;
  color: ${props => (props.opened ? "var(--white)!important" : "var(--white)")};

  /*
  &:hover {
    background: var(--main-brand)!important;
    color: var(--light-shades)!important;
  }
  */
  /*
  & > .svg-inline--fa {
    max-height: 18px;
  }
  */
`

/**
 * React component (JSX).
 */
export default (props) => {

  const container = React.useRef()

  const handleClickOutside = event => {
    if (container.current && !container.current.contains(event.target)) {
      setOpen(false)
    }
  }

  const [open, setOpen] = useState(false)

  // Outside dropdown click listener (only run when opened)
  useEffect(() => {
    // componentDidMount()
    // Runs after the first render() lifecycle
    // Anything in here is fired on component mount.
    document.addEventListener("mousedown", handleClickOutside)
    
    return () => {
      // componentWillUnmount()
      // Anything in here is fired on component unmount.
      document.removeEventListener("mousedown", handleClickOutside)
    }
  }, [])

  return (
    <NavItemWrap id={props.id} ref={container}>
      <IconButton className="ddmenu" opened={open} onClick={() => {setOpen(!open)}} aria-label="Options Menu">
        {open ? props.iconOpened : props.iconClosed}
      </IconButton>
      {open && props.children}
    </NavItemWrap>
  )

}