/**
 * WrapRootElement.
 * 
 * Implement the Gatsby's WrapRootElement Browser API.
 * 
 * @see: https://www.gatsbyjs.org/docs/browser-apis/
 *
 * @param	element:	The Page element (React component).
 * @param	props: 		The Page properties. Provide same data to Layout as Page element will get
 *           			  including location, data, etc - you don't need to pass it.
 * 
 * @return	Layout:		Layout React Component (JSX).
 */

/**
 * Dependencies.
 */
import * as React from 'react'
import GlobalContextProvider from './GlobalContextProvider'

/**
 * Root Wrapper React component (JSX).
 */
export default ({ element }) => {
  return <GlobalContextProvider>{element}</GlobalContextProvider>
}
