/**
 * SEO
 * 
 * Generate all SEO related meta.
 */

/**
 * Dependencies.
 */
import React from 'react'
import PropTypes from 'prop-types'
import { Helmet } from 'react-helmet'
import { GatsbySeo, ArticleJsonLd } from 'gatsby-plugin-next-seo'
import { useLocation } from '@reach/router'
import { useStaticQuery, graphql } from 'gatsby'

/**
 * Component (JSX).
 */
const SEO = ({ category, tags, title, description, lang, pageId, image, template, author, date, languageAlternates }) => {

  const { pathname } = useLocation()
  const { site } = useStaticQuery(query)

  const {
    siteTitle,
    titleSeparator,
    defaultDescription,
    siteUrl,
    defaultImage,
    ogLogo,
    locales
  } = site.siteMetadata

  const { langsIso } = locales

  const seo = {
    title: title || siteTitle,
    description: description || defaultDescription,
    image: `${siteUrl}${image || defaultImage}`,
    url: `${siteUrl}${pathname}`,
    langIso: langsIso.find(el => el.startsWith(lang))
  }

  const finalTitleTemplate = (pageId === 'home') ? `${siteTitle} ${titleSeparator} %s` : `%s ${titleSeparator} ${siteTitle}`
  const ogArticle = template === 'post' ? {
    publishedTime: date,
    modifiedTime: date,
    section: category,
    author: `${siteUrl}/about/`,
    tags: tags,
  } : null

  let hrefLangArray
  if (languageAlternates) {
    hrefLangArray = [
      { hrefLang: lang, href: seo.url}
    ]
    languageAlternates.forEach(langAlt => {
    //languageAlternates.map(langAlt => {
      hrefLangArray.push( { hrefLang: langAlt[0], href: siteUrl.concat(langAlt[1]) } )
    })
  }  

  return (
    <>
    <GatsbySeo
      language={lang}
      title={seo.title}
      description={seo.description}
      titleTemplate={finalTitleTemplate}
      canonical={seo.url}
      metaTags={[{
        name: 'image',
        content: seo.image
      },
      {
        name: 'copyright',
        content: siteTitle
      }]}
      languageAlternates={hrefLangArray}
      openGraph={{
        type: template === 'post' ? 'article' : 'website',
        url: seo.url,
        title: seo.title,
        description: seo.description,
        images: [
          {
            url: seo.image,
            width: 800,
            height: 600,
            alt: seo.title,
          },
        ],
        site_name: siteTitle,
        locale: seo.langIso,        
        article: ogArticle ? ogArticle : null,
      }}
      twitter={{
        cardType: 'summary_large_image',
        site: author?.links?.twitter ? author.links.twitter : null,
        handle: author?.links?.twitter ? author.links.twitter : null,
      }}
    />
    <ArticleJsonLd
      url={seo.url}
      headline={seo.title}
      images={[
        seo.image,
      ]}
      datePublished={date}
      dateModified={date}
      authorName={author?.name}
      publisherName={siteTitle}
      publisherLogo={`${siteUrl}${ogLogo}`}
      description={seo.description}
      /*
      overrides={{
        '@type': 'BlogPosting', // set's this as a blog post.
      }}
      */
    />
    <Helmet>
      <body id={pageId} className={template} />
    </Helmet>
  </>)
}

/**
 * Properties.
 */
SEO.propTypes = {
  title: PropTypes.string,
  description: PropTypes.string,
  image: PropTypes.string,
  article: PropTypes.bool,
  author: PropTypes.object
}

SEO.defaultProps = {
  title: null,
  description: null,
  image: null,
  article: false,
  author: null
}

/**
 * Static Query.
 */
const query = graphql`
  query SEO {
    site {
      siteMetadata {
        siteTitle: title
        titleSeparator
        defaultDescription: description
        siteUrl
        siteLogo
        defaultImage: ogimage
        ogLogo
        locales {
          langsIso
        }
      }
    }
  }
`

export default SEO
